<template>
  <div>
    <!-- 回款记录列表页 -->
    <div class="con_box">
      <div class="head_filter_box">
        <div class="filter_list">
            <div class="filter_item">
                <el-input
                    v-model="keyword"
                    type="text"
                    size="mini"
                    placeholder="编号/主题"
                ></el-input>
            </div>
            <div class="filter_item">
            <el-select size="mini" v-model="customerId" placeholder="客户">
                <el-option
                v-for="item in customerlist"
                :key="item.value"
                :label="item.label"
                :value="item.value"
                >
                </el-option>
            </el-select>
            </div>
            <div class="filter_item">
            <el-select size="mini" v-model="payment_typeId" placeholder="回款类型">
                <el-option
                v-for="item in payment_typelist"
                :key="item.value"
                :label="item.label"
                :value="item.value"
                >
                </el-option>
            </el-select>
            </div>
            <div class="filter_item">
            <el-select size="mini" v-model="empId" placeholder="收款人">
                <el-option
                v-for="item in emplist"
                :key="item.value"
                :label="item.label"
                :value="item.value"
                >
                </el-option>
            </el-select>
            </div>
            <div class="filter_item">
            <el-date-picker
                v-model="dates"
                type="daterange"
                range-separator="至"
                start-placeholder="收款日期"
                end-placeholder="收款日期"
                size="mini"
            >
            </el-date-picker>
            </div>
            <div class="filter_item">
            <el-button type="primary" size="mini"> 查询 </el-button>
            <el-button size="mini"> 重置 </el-button>
            </div>
        </div>
        <div class="filter_option">
            <el-button @click="newcreate" size="mini" type="primary">
                新建
            </el-button>
        </div>
      </div>
      <div class="main_table_box">
        <el-table
          ref="multipleTable"
          :data="tableData"
          border
          style="width: 100%"
          size="mini"
          :height="$store.state.innerheight - 190"
          :header-cell-style="{ background: '#f5f5f5' }"
        >
          <el-table-column label="回款编号" width="">
              <template #default="scope">
                <small>{{ scope.row.code }}</small>
                </template>
          </el-table-column>
          <el-table-column prop="title" label="回款主题" width="">
          </el-table-column>
          <el-table-column label="客户" width="">
              <template #default="scope">
                <small>{{ scope.row.cus_name }}</small>
                </template>
          </el-table-column>
          <el-table-column label="合同" width="">
            <template #default="scope">
              <small>{{ scope.row.contract }}</small>
            </template>
          </el-table-column>
          <el-table-column label="回款类型" width="">
            <template #default="scope">
              <small>{{ scope.row.Collection_type }}</small>
            </template>
          </el-table-column>
          <el-table-column label="回款金额" width="">
            <template #default="scope">
              <small>￥</small>
              <small>{{ scope.row.amount }}</small>
            </template>
          </el-table-column>
          <el-table-column label="收款人" width="">
            <template #default="scope">
              <small>{{ scope.row.emp_name }}</small>
            </template>
          </el-table-column>
          <el-table-column prop="collection_date" label="收款日期" width="">
              <template #default="scope">
              <small>{{ scope.row.collection_date }}</small>
            </template>
          </el-table-column>
          <el-table-column prop="business_status" label="业务类型" width="">
              <template #default="scope">
              <small>{{ scope.row.business_status }}</small>
            </template>
          </el-table-column>
          <el-table-column fixed="right" label="操作" width="200">
            <template #default>
              <el-button @click="returnamount" type="text" size="mini">退款</el-button>
              <el-button type="text" size="mini" @click="handleEdit()">编辑</el-button>
              <el-button type="text" size="mini" class="removeclass">删除</el-button>
            </template>
          </el-table-column>
        </el-table>
      </div>
      <div class="footer_box" style="margin-top: 10px">
        <el-pagination
          v-model:currentPage="currentPage4"
          :page-sizes="[20, 50, 70, 100]"
          :page-size="20"
          layout="total, sizes, prev, pager, next, jumper"
          :total="400"
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
        >
        </el-pagination>
      </div>
    </div>
  </div>
</template>

<script>
import { getCurrentInstance, reactive, toRefs } from "vue";

export default {
  setup() {
    const { proxy } = getCurrentInstance()
    const state = reactive({
        tableData:[
          {
            code:'22',
            title:'111',
            cus_name:'333',
            contract:'444',
            Collection_type:'5555',
            amount:'666',
            emp_name:'7777',
            collection_date:'8888',
            business_status:'9999'
          }
        ],
      keyword: "",
      customerId: "",
      customerlist: [],
      empId: "",
      emplist: [],
      dates: "",
      payment_typeId:'',
      payment_typelist:[],
      currentPage4: 1,
    });
    const handleSizeChange = (val) => {
      console.log(`${val} items per page`);
    };
    const handleCurrentChange = (val) => {
      console.log(`current page: ${val}`);
    };
    const handleEdit = () => {
      proxy.$router.push({
        path: "/Collection_new",
        query: {
          obj: {},
          isedit: 0,
          isouter:1
        },
      });
    }
    const newcreate = () => {
      proxy.$router.push({
        path: "/Collection_new",
        query: {
          obj: {},
          isedit: 0,
          isouter:1
        },
      });
    }
    const returnamount = () => {
      proxy.$router.push({
        path:"/refund_details",
        query:{
          isedit:1,
          isouter:1
        }
      });
    }
    return {
      ...toRefs(state),
      handleSizeChange,
      handleCurrentChange,
      newcreate,
      returnamount,
      handleEdit
    };
  },
};
</script>

<style scoped>
.head_filter_box {
  display: flex;
  align-items: flex-start;
  padding: 5px 0;
  padding-top: 0;
  box-sizing: border-box;
  justify-content: space-between;
}
.filter_list{
    display: flex;
    align-items: center;
    width: calc(100% - 100px);
    flex-wrap: wrap;
}
.filter_item {
  margin-right: 5px;
  margin-top: 5px;
}
.filter_option{
    margin-top: 5px;
}
.removeclass{
    color: red;
}
</style>
<style>
.filter_item .el-range-editor--mini.el-input__inner {
  width: 230px;
}
</style>