<template>
  <div>
    <!-- 退款记录列表 -->
    <div class="con_box">
      <div class="head_filter_box">
        <div class="filter_item">
          <el-input
            v-model="keyword"
            type="text"
            size="mini"
            placeholder="编号/主题"
          ></el-input>
        </div>
        <div class="filter_item">
          <el-select size="mini" v-model="customerId" placeholder="客户">
            <el-option
              v-for="item in customerlist"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            >
            </el-option>
          </el-select>
        </div>
        <div class="filter_item">
          <el-select size="mini" v-model="empId" placeholder="退款人">
            <el-option
              v-for="item in emplist"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            >
            </el-option>
          </el-select>
        </div>
        <div class="filter_item">
          <el-date-picker
            v-model="dates"
            type="daterange"
            range-separator="至"
            start-placeholder="退款日期"
            end-placeholder="退款日期"
            size="mini"
          >
          </el-date-picker>
        </div>
        <div class="filter_item">
          <el-button type="primary" size="mini"> 查询 </el-button>
          <el-button size="mini"> 重置 </el-button>
          <el-button @click="newcreate" type="primary" size="mini"> 新建 </el-button>
        </div>
      </div>
      <div class="main_table_box">
        <el-table
          ref="multipleTable"
          :data="tableData"
          border
          style="width: 100%"
          size="mini"
          :height="$store.state.innerheight - 155"
          :header-cell-style="{ background: '#f5f5f5' }"
        >
          <el-table-column prop="code" label="退款编号" width="">
          </el-table-column>
          <el-table-column prop="title" label="退款主题" width="">
          </el-table-column>
          <el-table-column prop="cus_name" label="客户" width="">
          </el-table-column>
          <el-table-column label="业务单" width="">
            <template #default="scope">
              <small>{{ scope.row.contract }}</small>
            </template>
          </el-table-column>
          <el-table-column label="退款原因" width="">
            <template #default="scope">
              <small>{{ scope.row.contract }}</small>
            </template>
          </el-table-column>
          <el-table-column label="退款金额" width="">
            <template #default="scope">
              <small>￥</small>
              <small>{{ scope.row.business_amount }}</small>
            </template>
          </el-table-column>
          <el-table-column prop="emp_name" label="退款人" width="">
          </el-table-column>
          <el-table-column prop="emp_date" label="退款日期" width="">
          </el-table-column>
          <el-table-column prop="emp_date" label="业务类型" width="">
          </el-table-column>
          <el-table-column fixed="right" label="操作" width="100">
            <template #default>
              <el-button type="text" size="mini">编辑</el-button>
              <el-button type="text" size="mini" class="removeclass">删除</el-button>
            </template>
          </el-table-column>
        </el-table>
      </div>
      <div class="footer_box" style="margin-top: 10px">
        <el-pagination
          v-model:currentPage="currentPage4"
          :page-sizes="[20, 50, 70, 100]"
          :page-size="20"
          layout="total, sizes, prev, pager, next, jumper"
          :total="400"
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
        >
        </el-pagination>
      </div>
    </div>
  </div>
</template>

<script>
import { getCurrentInstance, reactive, toRefs } from "vue";

export default {
  setup() {
    const { proxy } = getCurrentInstance()
    const state = reactive({
        tableData:[],
      keyword: "",
      customerId: "",
      customerlist: [],
      empId: "",
      emplist: [],
      dates: "",
      currentPage4: 1,
    });
    const handleSizeChange = (val) => {
      console.log(`${val} items per page`);
    };
    const handleCurrentChange = (val) => {
      console.log(`current page: ${val}`);
    };
    const newcreate = () => {
      proxy.$router.push({
        path:"/refund_details",
        query:{
          isedit:1,
          isouter:1
        }
      });
    }
    return {
      ...toRefs(state),
      handleSizeChange,
      handleCurrentChange,
      newcreate
    };
  },
};
</script>

<style scoped>
.head_filter_box {
  display: flex;
  align-items: center;
  padding: 5px 0;
  box-sizing: border-box;
}
.filter_item {
  margin-right: 5px;
  display: flex;
}
.removeclass{
    color: red;
}
</style>
<style>
.filter_item .el-range-editor--mini.el-input__inner {
  width: 230px;
}
</style>