<template>
  <div>
    <!-- 应收账单列表页 -->
    <div class="con_box">
      <div class="head_filter_box">
        <div class="filter_item">
          <el-input
            v-model="keyword"
            type="text"
            size="mini"
            placeholder="业务单编号/主题"
          ></el-input>
        </div>
        <div class="filter_item">
          <el-select size="mini" v-model="customerId" placeholder="客户">
            <el-option
              v-for="item in customerlist"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            >
            </el-option>
          </el-select>
        </div>
        <div class="filter_item">
          <el-select size="mini" v-model="empId" placeholder="业务员">
            <el-option
              v-for="item in emplist"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            >
            </el-option>
          </el-select>
        </div>
        <div class="filter_item">
          <el-date-picker
            v-model="dates"
            type="daterange"
            range-separator="至"
            start-placeholder="业务日期"
            end-placeholder="业务日期"
            size="mini"
          >
          </el-date-picker>
        </div>
        <div class="filter_item">
          <el-button type="primary" size="mini"> 查询 </el-button>
          <el-button size="mini"> 重置 </el-button>
        </div>
      </div>
      <div class="main_table_box">
        <el-table
          ref="multipleTable"
          :data="tableData"
          border
          style="width: 100%"
          size="mini"
          :height="$store.state.innerheight - 155"
          :header-cell-style="{ background: '#f5f5f5' }"
        >
          <el-table-column prop="cus_name" label="客户" width="">
          </el-table-column>
          <el-table-column prop="business_type" label="业务类型" width="">
          </el-table-column>
          <el-table-column label="合同" width="">
            <template #default="scope">
              <small>{{ scope.row.contract }}</small>
            </template>
          </el-table-column>
          <el-table-column label="业务金额" width="">
            <template #default="scope">
              <small>￥</small>
              <small>{{ scope.row.business_amount }}</small>
            </template>
          </el-table-column>
          <el-table-column label="已收款" width="">
            <template #default="scope">
              <small>￥</small>
              <small>{{ scope.row.Received }}</small>
            </template>
          </el-table-column>
          <el-table-column label="未收款" width="">
            <template #default="scope">
              <small>￥</small>
              <small>{{ scope.row.Uncollected }}</small>
            </template>
          </el-table-column>
          <el-table-column prop="emp_name" label="业务员" width="">
          </el-table-column>
          <el-table-column prop="emp_date" label="业务日期" width="">
          </el-table-column>
          <el-table-column fixed="right" label="操作" width="100">
            <template #default>
              <el-button @click="returnamount" type="text" size="mini">回款</el-button>
            </template>
          </el-table-column>
        </el-table>
      </div>
      <div class="footer_box" style="margin-top: 10px">
        <el-pagination
          v-model:currentPage="currentPage4"
          :page-sizes="[20, 50, 70, 100]"
          :page-size="20"
          layout="total, sizes, prev, pager, next, jumper"
          :total="400"
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
        >
        </el-pagination>
      </div>
    </div>
    
  </div>
</template>

<script>
import { getCurrentInstance, reactive, toRefs } from "vue";

export default {
  
  setup() {
    const { proxy } = getCurrentInstance()
    const state = reactive({
        tableData:[
          {
            cus_name:'1111111',
            business_type:'222222222',
            contract:'3333',
            business_amount:'4444',
            Received:'5555',
            Uncollected:'4444',
            emp_name:'4555',
            emp_date:'566'
          }
        ],
      keyword: "",
      customerId: "",
      customerlist: [],
      empId: "",
      emplist: [],
      dates: "",
      currentPage4: 1,
    });
    const handleSizeChange = (val) => {
      console.log(`${val} items per page`);
    };
    const handleCurrentChange = (val) => {
      console.log(`current page: ${val}`);
    };
    const returnamount = () => {
      console.log('进入回款详情页')
      proxy.$router.push({
        path: "/Collection_new",
        query: {
          obj: {},
          isedit: 0,
          isouter:1
        },
      });
    }
    return {
      ...toRefs(state),
      handleSizeChange,
      handleCurrentChange,
      returnamount
    };
  },
};
</script>

<style scoped>
.head_filter_box {
  display: flex;
  align-items: center;
  padding: 5px 0;
  box-sizing: border-box;
}
.filter_item {
  margin-right: 5px;
}
</style>
<style>
.filter_item .el-range-editor--mini.el-input__inner {
  width: 230px;
}
</style>